<template>
  <div class="partyBuild">
    <!-- 通知公告 -->
    <div class="new-content">
      <div class="top-title">党建专栏</div>
      <div v-for="(item, i) in newList" :key="i" class="articals">
        <p class="clearfix">
          <span v-if="item.titleofarticle && item.titleofarticle.length < 66" @click="toDetail(item)">{{item.titleofarticle}}</span>
          <span v-else @click="toDetail(item)">{{item.titleofarticle.substring(0, 65) + '...'}}</span>
          <span>{{item.time}}</span>
        </p>
      </div>
      <el-pagination
        v-if="newList && newList.length > 0"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="pageNum"
        background
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalCount">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import {dateFormat} from '@/filters/index'
export default {
  name: 'partyBuild',
  data () {
    return {
      newList: [],
      currentPage: 0,
      totalCount: 100,
      pageNum: 10
    }
  },
  methods: {
    handleSizeChange (val) {
      this.pageNum = val
      this.getList()
    },
    handleCurrentChange (val) {
      this.currentPage = val
      this.getList()
    },
    toDetail (item) {
      window.sessionStorage.partDetail = JSON.stringify(item)
      this.$router.push('/partyBuildDetail')
    },
    async getList () {
      this.newList = []
      await this.$store.dispatch('articleGet', {
        pageSize: this.pageNum,
        pageNum: this.currentPage,
        type: 4
      }).then((res) => {
        if (res && res.list && res.list.length > 0) {
          this.newList = res.list.map(item => ({
            ...item,
            date: dateFormat(item.time, 'yyyy-MM-dd')
          }))
          this.totalCount = res.total
        }
      })
    }
  },
  created () {
    this.getList()
  }
}
</script>

<style lang="scss">
.partyBuild {
  padding: 20px 0;
  > .new-content {
    .top-title {
      background: #CC0000;
    }
  }
}
</style>



