<template>
  <div class="notice-detail">
    <!-- 通知详情-->
    <div class="detail-box">
      <div class="return">当前位置：<span class="prevlevel" @click="toPrev">通知通告</span> / <span class="now">通知详情</span></div>
      <div class="artical-detail">
        <h2>{{artical.title}}</h2>
        <p class="des">
          <span class="author">作者：{{artical.author}}</span>
          <span class="date">日期：{{artical.date}}</span>
        </p>
        <div class="img-box" v-if="artical.picture && artical.picture.length > 0">
          <img :src="artical.picture" alt="" />
        </div>
        <div class="contents" v-html="artical.content"></div>
      </div>
    </div>
  </div>
</template>

<script>
import {dateFormat} from '@/filters/index'
export default {
  name: 'notice-detail',
  data () {
    return {
      artical: {
        title: '',
        date: '',
        author: '',
        content: '',
        picture: ''
      }
    }
  },
  methods: {
    toPrev () {
      this.$router.push('/notice')
    }
  },
  created () {
    let item = JSON.parse(window.sessionStorage.noticeDetail || {titleofarticle: '', time: '', authorofthzarticle: '', contentofthearticle: ''})
    if (!item) return
    this.artical = {
      title: item.titleofarticle,
      date: dateFormat(item.time, 'yyyy-MM-dd'),
      author: item.authorofthzarticle,
      content: item.contentofthearticle,
      picture: item.picture
    }
  }
}
</script>

<style lang="scss">
.notice-detail {
  padding: 20px 0;
  text-indent: 2em;
  white-space: pre-line;
  .img-box {
    margin-top: 20px;
    text-align: center;
    img {
      width: 60%;
      // height: 300px;
    }
  }
}
</style>



