<template>
  <div class="manage-three">
    <!-- 文件管理 -->
    <div class="manage-add">
      <el-button type="primary" size="mini" @click="addNew">新增</el-button>
    </div>
    <div class="tabs">
      <el-table
        :data="tableData"
        style="width: 100%">
        <el-table-column
          prop="fileheadings"
          label="文件名">
        </el-table-column>
        <el-table-column
          prop="filepeople"
          label="上传人"
          width="100">
        </el-table-column>
        <el-table-column
          prop="date"
          label="日期"
          width="150">
        </el-table-column>
        <el-table-column label="操作" width="100">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 30, 40, 50]"
        :page-size="pageNum"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalCount">
      </el-pagination>
    </div>
    <!-- 新增或编辑 -->
    <el-dialog
      title="新增文件"
      :visible.sync="addNewDialog"
      top="2%"
      width="40%">
      <el-form :model="ruleForm" status-icon :rules="rules2" ref="ruleForm" label-width="110px" class="demo-ruleForm">
        <el-form-item label="文件名称" prop="fileheadings">
          <el-input type="text" v-model="ruleForm.fileheadings" placeholder="请输入文章标题"></el-input>
        </el-form-item>
        <!-- <el-form-item label="文件上传人员" prop="authorofthzarticle">
          <el-input type="text" v-model="ruleForm.authorofthzarticle" placeholder="请输入作者名称"></el-input>
        </el-form-item> -->
        <el-form-item label="文件列表">
          <el-upload
            class="upload-demo"
            :action="action"
            :on-remove="handleRemove"
            :on-success="handleImgSuccess"
            :before-remove="beforeRemove"
            multiple
            :limit="1"
            name="file"
            :file-list="ruleForm.fileList">
            <el-button size="small" type="primary">附件上传</el-button>
            <!-- <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div> -->
          </el-upload>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="resetForm('ruleForm')">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 删除弹框 -->
    <el-dialog
      title="删除"
      :visible.sync="deleteDialog"
      width="30%">
      <span>确定删除该文章吗？</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deleteDialog = false">取 消</el-button>
        <el-button type="primary" @click="delSure">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import {success} from '@/api/index'
import url from '@/baseUrl'
import {dateFormat} from '@/filters/index'
export default {
  name: 'manage-three',
  data () {
    return {
      tableData: [],
      ruleForm: {
        fileheadings: '',
        fileenclosure: '',
        fileList: []
      },
      rules2: {
        fileheadings: [
          { required: true, trigger: 'blur', message: '文件名称不能为空' }
        ]
        // authorofthzarticle: [
        //   { required: true, trigger: 'blur', message: '上传人名称不能为空' }
        // ],
        // fileList: [
        //   { type: 'array', required: true, message: '请至少选择1个文件', trigger: 'change' }
        // ]
      },
      addNewDialog: false,
      deleteDialog: false,
      currentPage: 0,
      pageNum: 10,
      totalCount: 0,
      dialogImageUrl: '',
      fileUrl: '',
      inifoId: ''
    }
  },
  computed: {
    action () {
      return url + '/upload'
    }
  },
  methods: {
    handleDelete (index, row) {
      this.inifoId = row.id
      this.deleteDialog = true
    },
    addNew () {
      this.isChange = false
      this.addNewDialog = true
    },
    submitForm (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          await this.$store.dispatch('fileAdd', {
            fileheadings: this.ruleForm.fileheadings,
            filepeople: this.$store.state.username,
            fileenclosure: this.fileUrl
          })
        } else {
          return false
        }
        this.addNewDialog = false
        this.getList()
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
      this.addNewDialog = false
    },
    handleSizeChange (val) {
      this.pageNum = val
      this.getList()
    },
    handleCurrentChange (val) {
      this.currentPage = val
      this.getList()
    },
    handleRemove (file, fileList) {
      fileList = []
      this.fileUrl = ''
    },
    beforeRemove (file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`)
    },
    handleImgSuccess (res, file, fileList) {
      this.fileUrl = res ? res.data : ''
      success('上传成功')
    },
    async getList () {
      this.tableData = []
      await this.$store.dispatch('fileSelect', {
        pageNum: this.currentPage,
        pageSize: this.pageNum
      }).then((res) => {
        if (res && res.list && res.list.length > 0) {
          this.tableData = res.list.map((item, i) => ({
            ...item,
            date: dateFormat(item.time, 'yyyy-MM-dd HH:mm')
          }))
          this.totalCount = res.total
        }
      })
    },
    async delSure () {
      await this.$store.dispatch('fileDel', {
        groupId: this.inifoId
      }).then((res) => {
        this.deleteDialog = false
        this.getList()
      })
    }
  },
  created () {
    this.getList()
  }
}
</script>

<style lang="scss">
.manage-three {
  text-align: left;
  .manage-add {
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid #efefef;
    padding-left: 20px;
    box-sizing: border-box;
  }
  .tabs {
    padding: 20px;
    .el-table th:first-child {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }
    th {
      background: #f5f7fa;
    }
  }
  .el-form {
    .el-input {
      width: 90%;
    }
  }
  .el-pagination {
    margin-top: 20px;
    text-align: right;
  }
}
</style>



