<template>
  <div class="manage-two">
    <!-- 文档管理 -->
    <div class="manage-add">
      <el-button type="primary" size="mini" @click="addNew">新增</el-button>
    </div>
    <div class="choose">
      <span>文章类型：</span>
      <el-radio-group v-model="type" @change="radioChange">
        <el-radio :label="0">全部</el-radio>
        <el-radio :label="1">公司新闻</el-radio>
        <el-radio :label="2">通知通告</el-radio>
        <el-radio :label="3">生产信息</el-radio>
        <el-radio :label="4">党建专栏</el-radio>
      </el-radio-group>
    </div>
    <div class="tabs">
      <el-table
        :data="tableData"
        style="width: 100%">
        <el-table-column
          prop="titleofarticle"
          label="标题">
        </el-table-column>
        <el-table-column
          prop="authorofthzarticle"
          label="作者"
          width="100">
        </el-table-column>
        <el-table-column
          prop="date"
          label="日期"
          width="150">
        </el-table-column>
        <el-table-column label="操作" width="150">
          <template slot-scope="scope">
            <el-button
              :disabled="$store.state.userid.indexOf(Number(scope.row.columntype)) == -1"
              size="mini"
              @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
            <el-button
              :disabled="$store.state.userid.indexOf(Number(scope.row.columntype)) == -1"
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 30, 40, 50]"
        :page-size="pageNum"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalCount">
      </el-pagination>
    </div>
    <!-- 新增或编辑 -->
    <el-dialog
      :before-close="handleClose"
      :title="isChange ? '编辑文章' : '新增文章'"
      :visible.sync="addNewDialog"
      top="2%"
      width="40%">
      <el-form :model="ruleForm" status-icon :rules="rules2" ref="ruleForm" label-width="110px" class="demo-ruleForm">
        <el-form-item label="文章标题" prop="titleofarticle">
          <el-input type="text" v-model="ruleForm.titleofarticle" placeholder="请输入文章标题"></el-input>
        </el-form-item>
        <el-form-item label="文章作者" prop="authorofthzarticle">
          <el-input type="text" v-model="ruleForm.authorofthzarticle" placeholder="请输入作者名称"></el-input>
        </el-form-item>
        <el-form-item label="图片新闻">
          <el-switch
            v-model="ruleForm.isPicture"
            active-text="ON"
            inactive-text="OFF">
          </el-switch>
        </el-form-item>
        <el-form-item label="图片选择" prop="picture" v-if="ruleForm.isPicture">
          <el-upload
            :action="action"
            list-type="picture-card"
            :before-upload="beforeImgUpload"
            :on-success="handleImgSuccess"
            :on-error="handleImgError"
            :on-remove="handleImgMove"
            :limit="1"
            :file-list="fileList"
            :data="datas"
            name="file">
            <i class="el-icon-plus"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="所属栏目" prop="columntype">
          <el-select v-model="ruleForm.columntype" placeholder="请选择">
            <el-option
              v-for="item in options"
              :disabled="item.disabled"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="文章内容" prop="contentofthearticle">
          <el-input type="textarea" placeholder="请输入文章内容" v-model="ruleForm.contentofthearticle" :autosize="{ minRows: 10, maxRows: 16}" resize="none"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="resetForm('ruleForm')">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 删除弹框 -->
    <el-dialog
      title="删除"
      :visible.sync="deleteDialog"
      width="30%">
      <span>确定删除该文章吗？</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deleteDialog = false">取 消</el-button>
        <el-button type="primary" @click="delSure">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import url from '@/baseUrl'
import {success, notice} from '@/api/index'
import {dateFormat} from '@/filters/index'
export default {
  name: 'manage-two',
  data () {
    var titleofarticle = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('文章标题不能为空'))
      } else {
        if (value.trim().length > 50) {
          callback(new Error('文章标题最大长度为50个字符'))
        }
        callback()
      }
    }
    var authorofthzarticle = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('作者名称不能为空'))
      } else {
        if (value.trim().length > 20) {
          callback(new Error('作者名称最大长度为20个字符'))
        }
        callback()
      }
    }
    return {
      datas: {
        token: '',
        key: 'e.jpg'
      },
      options: [
        {
          value: '1',
          label: '公司新闻',
          disabled: true
        },
        {
          value: '2',
          label: '通知通告',
          disabled: true
        },
        {
          value: '3',
          label: '生产信息',
          disabled: true
        },
        {
          value: '4',
          label: '党建专栏',
          disabled: true
        }
      ],
      value: '',
      tableData: [],
      ruleForm: {
        titleofarticle: '',
        authorofthzarticle: '',
        picture: '',
        isPicture: false,
        columntype: '',
        contentofthearticle: ''
      },
      rules2: {
        titleofarticle: [
          { required: true, validator: titleofarticle, trigger: 'blur' }
        ],
        authorofthzarticle: [
          { required: true, validator: authorofthzarticle, trigger: 'blur' }
        ],
        contentofthearticle: [
          { required: true, trigger: 'blur', message: '文章内容不能为空' }
        ],
        columntype: [
          { required: true, trigger: 'change', message: '请选择文章类型' }
        ]
      },
      addNewDialog: false,
      isChange: false,
      deleteDialog: false,
      currentPage: 0,
      pageNum: 10,
      totalCount: 0,
      dialogImageUrl: '',
      infoId: '',
      type: 0,
      infoUserId: '',
      fileName: '',
      imgUrl: '',
      fileList: []
    }
  },
  methods: {
    handleEdit (index, row) {
      this.fileList = []
      this.isChange = true
      this.infoId = row.id
      let imgList = row.picture.split(',')
      this.fileList = imgList.map(item => ({
        url: item
      }))
      this.imgUrl = row.imgUrl
      this.ruleForm = {
        titleofarticle: row.titleofarticle,
        authorofthzarticle: row.authorofthzarticle,
        // picture: this.imgUrl,
        isPicture: (row.picture && row.picture.length > 0) ? Boolean(1) : Boolean(0),
        contentofthearticle: row.contentofthearticle,
        columntype: Number(row.columntype) === 1
          ? '公司新闻' : (Number(row.columntype) === 2)
            ? '通知通告' : (Number(row.columntype) === 3)
              ? '生产信息' : (Number(row.columntype) === 4)
                ? '党建专栏' : ''
      }
      this.addNewDialog = true
    },
    handleDelete (index, row) {
      this.deleteDialog = true
      this.infoId = row.id
      this.infoUserId = row.columntype
    },
    addNew () {
      this.isChange = false
      this.fileList = []
      this.ruleForm = {
        titleofarticle: '',
        authorofthzarticle: '',
        picture: '',
        isPicture: false,
        columntype: '',
        contentofthearticle: ''
      }
      this.addNewDialog = true
    },
    // 新增
    submitForm (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          if (this.isChange) {
            let types = this.ruleForm.columntype === '公司新闻'
              ? '1' : (this.ruleForm.columntype === '通知通告')
                ? '2' : (this.ruleForm.columntype === '生产信息')
                  ? '3' : (this.ruleForm.columntype === '党建专栏')
                    ? '4' : '' || this.ruleForm.columntype
            let datas = {
              id: this.infoId,
              authorofthzarticle: this.ruleForm.authorofthzarticle,
              columntype: types,
              contentofthearticle: this.ruleForm.contentofthearticle,
              picture: this.imgUrl,
              titleofarticle: this.ruleForm.titleofarticle
            }
            await this.$store.dispatch('articleUpdate', datas)
          } else {
            let datas = {
              authorofthzarticle: this.ruleForm.authorofthzarticle,
              columntype: this.ruleForm.columntype,
              contentofthearticle: this.ruleForm.contentofthearticle,
              picture: this.imgUrl,
              titleofarticle: this.ruleForm.titleofarticle
            }
            await this.$store.dispatch('articleCreate', datas)
          }
          this.$refs[formName].resetFields()
          this.addNewDialog = false
          this.getList()
          this.imgUrl = ''
          this.fileList = []
        } else {
          return false
        }
      })
    },
    handleClose () {
      this.fileList = []
      this.imgUrl = ''
      this.addNewDialog = false
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
      this.ruleForm = {
        titleofarticle: '',
        authorofthzarticle: '',
        picture: '',
        isPicture: false,
        columntype: '',
        contentofthearticle: ''
      }
      this.addNewDialog = false
    },
    handleSizeChange (val) {
      this.pageNum = val
      this.getList()
    },
    handleCurrentChange (val) {
      this.currentPage = val
      this.getList()
    },
    handleRemove (file, fileList) {
    },
    handlePictureCardPreview (file) {
      this.dialogImageUrl = file.url
      this.dialogVisible = true
    },
    // 删除
    async delSure () {
      this.deleteDialog = false
      await this.$store.dispatch('articleDel', {
        groupId: this.infoId,
        userids: this.infoUserId
      }).then((res) => {
        this.getList()
      })
    },
    async getList () {
      this.tableData = []
      await this.$store.dispatch('articleGet', {
        pageSize: this.pageNum,
        pageNum: this.currentPage,
        type: Number(this.type) === 0 ? '' : this.type
      }).then((res) => {
        if (res && res.list && res.list.length > 0) {
          this.tableData = res.list.map(item => ({
            ...item,
            date: dateFormat(item.time, 'yyyy-MM-dd')
          }))
          this.totalCount = res.total
        }
      })
    },
    // 单选框切换监听事件
    radioChange () {
      this.getList()
    },
    // 上传图片之前判断
    beforeImgUpload (file) {
      this.datas.fileName = file.name
      const isImage = file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'image/PNG' || file.type === 'image/JPG' || file.type === 'image/JPEG'
      const isLt30kb = file.size / 1024 / 1024 < 1

      if (!isImage) {
        this.$message.error('图片格式应该为 png/jpg/jpeg/JPG/JPEG/PNG')
      }
      if (!isLt30kb) {
        this.$message.error('图片大小不能超过 1M!')
      }
      return isImage && isLt30kb
    },
    // 上传logo图片成功
    handleImgSuccess (res, file, fileList) {
      this.imgUrl = res ? res.data : ''
      success('上传成功')
    },
    handleImgError (file, fileList) {
      fileList.splice(0, 1)
      notice('上传失败,请检查图片格式是否为png、jpg、jpeg、PNG、JPG、JPEG')
    },
    // 移除图片图片
    handleImgMove (file, fileList) {
      // this.imgList = fileList
      this.imgUrl = ''
    },
    handleExceed (files, fileList) {
      notice('最多上传一张图片')
    }
  },
  computed: {
    action () {
      return url + '/upload'
    }
  },
  async created () {
    // let qns = await this.$store.dispatch('qn')
    // this.datas.token = qns.result.token
    this.getList()
    let xi = this.$store.state.userid.split(',')
    this.options.forEach((item, i) => {
      xi.forEach((items, ind) => {
        if (item.value === xi[ind]) {
          item.disabled = false
        }
      })
    })
  }
}
</script>

<style lang="scss">
.manage-two {
  text-align: left;
  .manage-add {
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid #efefef;
    padding-left: 20px;
    box-sizing: border-box;
  }
  > .choose {
    padding-top: 20px;
    padding-left: 20px;
    box-sizing: border-box;
  }
  .tabs {
    padding: 20px;
    .el-table th:first-child {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }
    th {
      background: #f5f7fa;
    }
  }
  .el-form {
    .el-input {
      width: 90%;
    }
  }
  .el-pagination {
    margin-top: 20px;
    text-align: right;
  }
}
</style>



