<template>
  <div class="download">
    <!-- 新闻 -->
    <div class="new-content">
      <div class="top-title">文件下载</div>
      <div v-for="(item, i) in newList" :key="i" class="articals">
        <p class="clearfix">
          <span v-if="item.fileheadings && item.fileheadings.length < 66">{{item.fileheadings}}</span>
          <span v-else>{{item.fileheadings.substring(0, 65) + '...'}}</span>
          <span><el-button type="primary" size="mini" @click="download(item)">下载</el-button></span>
        </p>
      </div>
      <el-pagination
        v-if="newList && newList.length > 0"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="pageNum"
        background
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalCount">
      </el-pagination>
    </div>
  </div>
</template>

<script>
// import {success, notice} from '@/api/index'
import {dateFormat} from '@/filters/index'
export default {
  name: 'download',
  data () {
    return {
      newList: [],
      currentPage: 0,
      totalCount: 100,
      pageNum: 10
    }
  },
  methods: {
    handleSizeChange (val) {
      this.pageNum = val
      this.getList()
    },
    handleCurrentChange (val) {
      this.currentPage = val
      this.getList()
    },
    download (item) {
      window.location.href = item.fileenclosure
    },
    async getList () {
      this.newList = []
      await this.$store.dispatch('fileSelect', {
        pageNum: this.currentPage,
        pageSize: this.pageNum
      }).then((res) => {
        if (res && res.list && res.list.length > 0) {
          this.newList = res.list.map((item, i) => ({
            ...item,
            date: dateFormat(item.time, 'yyyy-MM-dd HH:mm')
          }))
          this.totalCount = res.total
        }
      })
    }
  },
  created () {
    this.getList()
  }
}
</script>

<style lang="scss">
.download {
  padding: 20px 0;
  > .new-content {
    .top-title {
      background: #356BA5;
    }
  }
}
</style>



